body,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: "Raleway", sans-serif
  }

body {
  font-size: .875rem;
}

.feather {
  width: 16px;
  height: 16px;
  vertical-align: text-bottom;
}

/*
 * Sidebar
 */

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 0;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

.sidebar-sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 48px; /* Height of navbar */
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}

.sidebar .nav-link.active {
  color: #007bff;
}

.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}

/*
 * Navbar
 */

.navbar-brand {
  padding-top: .75rem;
  padding-bottom: .75rem;
  font-size: 1rem;
  background-color: rgba(0, 0, 0, .25);
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .25);
}


/*
 *  Article
 */

#showfile {
  font-size: large;
  margin-left: 2vw;
}

#showfile h1,
#showfile h2,
#showfile h3,
#showfile h4,
#showfile h5,
#showfile h6{
  margin: 2vw;
}
#showfile h1,
#showfile h2{
  border-bottom: #dfe2e5 solid 1px;
}

#showfile img{
   display: block;
   width : 30vw;
   padding-left:2vw;
 }

#showfile code{
   background:#dfe2e5;
   color: black;
   padding:3px;
   
   border-radius: 3px;
 }

 #showfile blockquote{
  border-left: #dfe2e5 solid 4px;
  padding-left: 2rem;
 }
 
 #showfile p,
 #showfile ul,
 #showfile ol ,
 #showfile blockquote{
  margin-left:2vw;
 }


